import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import CV from '@/components/CV'
import Blog from '@/components/Blog'
import ReadMe from '@/components/ReadMe'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      meta: {
        title: '首页'
      }
    },
    {
      path: '/cv',
      name: 'CV',
      component: CV,
      meta: {
        title: '简历'
      }
    },
    {
      path: '/blog',
      name: 'Blog',
      component: Blog,
      meta: {
        title: '博客'
      }
    },
    {
      path: '/readme',
      name: 'ReadMe',
      component: ReadMe,
      meta: {
        title: '关于项目'
      }
    }
  ]
})



