const state = {
  author: 'Xu Fengli',
  title: "Xu Fengli's CV",
  screenH: document.documentElement.clientHeight || document.body.clientHeight,
  footNavBar: [
    {name: 'Home', isActive: false, path: '/home'},
    {name: 'CV', isActive: false, path: '/cv'},
    {name: 'Blog', isActive: false, path: '/blog'},
    {name: 'ReadMe', isActive: false, path: '/readme'}
  ],
  currentPathName: '',
  wrapClass: 'show'
}

const getters = {
  getScreenH: state => state.screenH,
  getFootNavBar: state => state.footNavBar,
  getCurrentPathName: state => state.currentPathName,
  getWrapClass: state => state.wrapClass
}

const actions = {
  resetCurrentPathName ({commit}, name) {
    commit('resetCurrentPathName', name)
    commit('initActiveNav', name)
  }
}

const mutations = {
  initActiveNav (state, name) {
    for (let [key, val] of state.footNavBar.entries()) {
      let flag = false
      if (val.name === name) {
        if (state.footNavBar.isActive) {
          break
        }
        flag = true
      } else {
        flag = false
      }
      state.footNavBar[key].isActive = flag
    }
  },
  resetCurrentPathName (state, name) {
    state.currentPathName = name
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



