<template>
  <div id="footer">
    <ul class="m-foot-box">
      <router-link tag="li" v-for="(val,index) in footNavBar" :key="index" :class="val.isActive?'active':''" :to="val.path">
        {{ val.name }}
      </router-link>
    </ul>

  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  computed: mapGetters({
    footNavBar: 'getFootNavBar',
    routerName: 'getRouterName',
    currentPathName: 'getCurrentPathName'
  }),
  methods: mapActions([
  ]),
  created () {
    this.$store.dispatch('resetCurrentPathName', this.$route.name)
  }
}
</script>

<style lang="scss" scoped>
  $color1: #444;
  $color2: #3caaff;
  $color3: #999999;
  *{padding: 0;margin: 0;}
  ul,li{list-style: none;}
  .m-foot-box{
    border-top: 1px solid $color3;
    display: flex;
    color: $color1;
    text-align: center;
    li{
      flex:1;
      line-height: 50px;
      &.active,&:hover{
        color:$color2;
      }
    }
  }
</style>



