<template>
  <div id="app" :style="{height:screenH+'px'}">
    <div class="m-main-content">
      <router-view/>
    </div>
    <div class="m-footer"><foot-nav></foot-nav></div>
  </div>
</template>

<script>
import footNav from './components/public/footer'
import {mapGetters} from 'vuex'
export default {
  name: 'App',
  components: {
    footNav
  },
  computed: mapGetters({
    screenH: 'getScreenH'
  })
}
</script>

<style>
*{padding:0;margin:0;}
ul, li, dl, dt, dd {list-style: none;}
body{height:667px;overflow:hidden;}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  display: flex;
  flex-direction: column;
  flex-flow:column;
  height: 100%;
}
.m-main-content {
  flex: 1;
  overflow-y: auto;
  position: relative;
  background-color: #fafafa;
}
.m-footer {
  flex-grow: 0;
  height: 50px;
}

</style>



